import axios from 'axios'
import Scroll from "../../components/scroll/scroll"
import Loading from '../../components/loading/loading'
export default {
  name: "gift-paper",
  components:{Scroll,Loading},
  data(){
    return{
      id:"",
      pullup: true,
      dataList:[]
    }
  },
  created(){
    this.id=this.$route.params.id;
    this.getData(this.id)
  },

  methods:{
    searchMore() {
      if (this.dataList.length > 5) {
        this.$refs.loading.style.display = "flex"
        this.loading = true;
        setTimeout(() => {
          var last = this.dataList[this.dataList.length - 1];
          for (var i = 1; i <= 10; i++) {
            this.dataList.push(last + i);
          }
          this.$refs.loading.style.display = "none"
          this.loading = false;
        }, 2500);
      }
      else {
        this.$refs.loading.style.display = "none"
        this.loading = false;
      }
    },
    getData(id){
      axios.get("http://192.168.0.55:8899/customerLoginController.do?method=getMerchantsByAcId&activityId=402880b764efac030164efb41727000e&page=1&rows=10&loginType=2").then((res)=>{
        console.log(res.data)
        this.dataList=res.data.dataList
      })
    },
    selectItem(item){
      this.$router.push({
        path: `/shop/${item.merchantId}`
      })
    },
    //
    back() {
      this.$router.go(-1);
      this.$destroy();
    },
  }
}



