import {debounce} from "../../common/js/util";

export default {
  name: "search-box",
  props: {
    placeholder: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      query: ""
    }
  },
  methods: {
    //
    clear() {
      this.query = ""
    },
    //
    blur(){
      this.$refs.query.blur()
    }
  },
  created() {
    //
    this.$watch('query', debounce((newQuery) => {
      //
      this.$emit('query', newQuery)
    },200))
  }
}



