import cityData from "../../common/js/cityData"
import Loading from '../../components/loading/loading'
export default {
    name: "select-city",
    components:{Loading},
  data(){
    return{
      selectCity: {},
      cityList: [],
    }
  },
  created(){
    setTimeout(()=>{
      this.initCityList()
    },1000)
  },
  methods: {
    //
    back() {
      this.$router.go(-1);
      // this.$destroy()
    },
    //
    initCityList() {
      this.cityList = cityData
      this.$refs.loading.style.display = "none"
    },

    //  citycity
    clickCity(city) {
      // Vuex statecurCity
      this.$store.commit('updateCity', city.name);
      this.$store.commit('updateCode', city.zip);
      this.$router.go(-1);
    }
  },
}



