import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    curCity: '',
    code:"",
    isFirst:0,
    isLogin: false,
    activityId:"",
    routrtUrl:[],
    userInfo: {
    	avatarUrl: '',
    	city: '',
    	gender: '',
    	nickName: '',
    	province: ''
    },
  },
  mutations: {
    updateCity: (state, change) => {
      state.curCity = change
    },
    updateCode: (state, change) => {
      state.code = change
    },
    updateFirst: (state, change) => {
      state.isFirst = change
    },
    updateActivityId: (state, change) => {
      state.activityId = change
    },
    updateIsLogin: (state, change) => {
  		state.isLogin = change
  	},
  	cleanUserInfo: (state) => {
  		state.userInfo = {
  			avatarUrl: '',
	    	city: '',
	    	gender: '',
	    	nickName: '',
	    	province: ''
  		}
  	},

    updateUrl:(state,change)=>{
      state.routrtUrl=change
    },
    updateUser: (state, change) => {
      change.gender = change.gender === 1 ? '' : ''
      state.userInfo = change
    }
  }
})
export default store



