
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

import BScroll from 'better-scroll'
import Loading from '../../components/loading/loading'
import Scroll from '../scroll/scroll'
import Tab from '../../components/public/navbar'
import axios from 'axios'

export default {
components: {Swiper, SwiperItem, Scroll, Tab, ButtonTab, ButtonTabItem, Loading, BScroll},
name: "home",
data() {
return {
loading: false,
dataIndex: 0,
paperIndex: 0,
activityData: [],
id: 1,
swiperItemIndex: 0,
imgUrls: [
'http://gw.alicdn.com/imgextra/i4/164/TB2RhntAFGWBuNjy0FbXXb4sXXa_!!164-0-luban.jpg',
'http://img.alicdn.com/imgextra/i2/52/TB2IYJEjyMnBKNjSZFzXXc_qVXa_!!52-0-luban.jpg',
'http://gw.alicdn.com/imgextra/i1/127/TB2YC6RjrZnBKNjSZFKXXcGOVXa_!!127-0-lubanu.jpg',
'http://gw.alicdn.com/imgextra/i4/87/TB27UR8i5MnBKNjSZFCXXX0KFXa_!!87-0-lubanu.jpg',
'http://gw.alicdn.com/imgextra/i2/190/TB2mLdhA29TBuNjy0FcXXbeiFXa_!!190-0-lubanu.jpg'
],
dataList0: [],
dataList1: [
{
id: 1
},
{
id: 3
},
{
id: 4
},
{
id: 5
},
{
id: 5
},
{
id: 5
},
{
id: 5
},
{
id: 5
},
{
id: 5
},
{
id: 5
},
{
id: 5
}
],
iconMap: [],
good: {
goodId: '100',
img: 'http://gw3.alicdn.com/bao/uploaded/i4/1798920415/TB21J8JpAKWBuNjy1zjXXcOypXa_!!1798920415-0-item_pic.jpg_210x210.jpg',
mainTitle: '',
sydTitle: '',
distance: '1.7',
price: '38',
sales: '37',
},
}
},
mounted() {
},
created() {
console.log(this.$store.state.isLogin)
if (this.$store.state.isLogin) {
this.getActivityData();
this.getCategoryData();
}
},
methods: {
search() {
this.$router.push({
path: `/search`
})
},
loadMore() {
this.$refs.loading.style.display = "flex"
this.loading = true;
if (this.dataList0.length > 10) {
setTimeout(() => {
let last = this.dataList0[this.dataList0.length - 1];
for (let i = 1; i <= 10; i++) {
this.dataList0.push(last + i);
}
this.$refs.loading.style.display = "none"
this.loading = false;
}, 2500);
}
else {
this.$refs.loading.style.display = "none"
this.loading = false;
}
},

getDate(time) {
const date = new Date(time);
const Y = date.getFullYear() + '-';
const M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
const D = date.getDate() + ' ';
const h = date.getHours() + ':';
const m = date.getMinutes() + ':';
const s = date.getSeconds();
return Y + M + D + h + m + s;
},

getCategoryData() {
axios.get("http://192.168.0.55:8999/industryCategoryController.do?method=getIndustryCategorys&loginType=2").then((res) => {
console.log(res.data);
this.iconMap = res.data.dataList;
})
},
getActivityData() {
axios.get("http://192.168.0.55:8999/customerLoginController.do?method=getActivitys&page=1&rows=1&loginType=2").then((res) => {

console.log(res.data);
this.activityData = res.data.dataList;
})
},
isScroll() {
return true
},

selectItem(item) {
this.$router.push({
path: `/activityshoplist/${item.activityId}`
})
},
selectCgtegory(item) {
this.$router.push({
path: `/categoryshoplist/${item.industryCategoryId}`
})
},
siwtchpaper(index) {
this.dataIndex = index;
this.dataSwitec();
},
dataSwitec() {
const num = this.dataIndex;
if (num === 0) {
return this.dataList0;
}
else if (num === 1) {
return this.dataList1;
}
}
},

beforeDestroy() {
this.$store.commit("updateIsLogin", false);
this.activityData = [];
this.iconMap = [];
},
}



