
import $ from 'jquery'
import wx from 'weixin-js-sdk'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import axios from 'axios'
import Loading from '../../components/loading/loading'
export default {
name: "game",
components:{Alert,TransferDom,Loading},
data() {
return {
scanImg:false,
show:false,
flag: false,
Logo:[],
ColorLogo:[],
gameId:"",
merchants:[],
code_num:[],
url3:"",
activityId:""
}
},
created() {
$('.background').bind('load', function() {

});
},
mounted() {
$('.background').bind('load', function() {

});
this.InitWx();
wx.ready(function () {
this.play();
})
},

methods: {
showOrhide(){
this.scanImg=!this.scanImg;
},

gotoPaper(){
this.$router.replace({
path: `/exchangepaper`
})
this.$destroy();
},
InitWx(){
$.post('http://weixin20.shushangsoft.com/JsapiTicket/GetJsapiTicket/',
{ officialAccountId: 'gh_36d980f25e97', url: location.href.split('#')[0], jsApiLists: 'scanQRCode', debug: false }, function (config) {
wx.config({
debug: false, // ,apialertpclogpc
appId: config.appId,
timestamp: config.timestamp,
nonceStr: config.nonceStr,
signature: config.signature,
jsApiList: config.jsApiList
});

})

















wx.error(function (res) {
alert("" + res.errMsg);
});
},
clickCode(index) {
var that=this
wx.ready(function () {
wx.checkJsApi({
jsApiList: ['scanQRCode'],
success: function (res) {
}
});
wx.scanQRCode({
needResult: 1, // 01
scanType: ["qrCode"], // 
success: function (res) {
let result = res.resultStr; 
const boat=$('.boat')
if(index===4){
boat.css({"bottom":" 10%","left":"50%"})
that.showOrhide()

}
else if(index===3){
boat.css({"bottom":" 15%","left":"70%"})


}
else if(index===2){
boat.css({"bottom":" 38%","left":"32%"})

}
else if(index===1){
boat.css({"bottom":" 55%","left":"29%"})


}
else if(index===0){
boat.css({"bottom":" 68%","left":"52%"})


}
}
});

});

},
play() {
this.flag = true;
const audio = document.getElementById('music');
audio.play();
},

back() {
this.$router.go(-1);
this.$destroy()
},
setClass(index) {
if (index === 0) {
return "brand-position0"
}
else if (index === 1) {
return "brand-position1"
}
else if (index === 2) {
return "brand-position3"
}
else if (index === 3) {
return "brand-position4"
}
else if (index === 4) {
return "brand-position6"
}
}
}
}



