
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

import axios from 'axios'
export default {
name: "gift-paper",
data(){
return{
scanImg:false,
qrcodeImgUrl:"",
allPaper:[],
unUsePaper:[
"https://s1.ax1x.com/2018/07/28/PUdXy8.png",
],
usedPaper:[

],
active: 0,
paperIndex:0
}
},
components:{ButtonTab, ButtonTabItem},

created(){
this.getData()
},

methods:{
showCode(item){
this.scanImg=true;
this.qrcodeImgUrl="http://192.168.0.55:8899/customerLoginController.do?method=getCouponBarCode&type=2&couponId="+item.couponId+"&loginType=2"
},
hideIma(){
this.scanImg=false;
this.qrcodeImgUrl="";
},
getData(){
axios.get("http://192.168.0.55:8899/customerLoginController.do?method=getMyCoupon&page=1&rows=5&loginType=2").then((res)=>{
this.allPaper=res.data.dataList;
})
},
siwtchpaper(index){
this.index=index
},

back() {
this.$router.go(-1);
this.$destroy();
},
}
}



