import Vue from "vue"
import Router from "vue-router"
import Home from "../../components/Home/Home"
import Home2 from "../../components/Home2/Home2"
import Me from '../../components/Me/Me'
import hello from '../HelloWorld'
import LoaginPage from '../../components/Login/LoginPage'
import picture from '../../components/picture/picture'
import GiftPaper from '../../components/GiftPaper/GiftPaper'
import ExChangePaper from '../../components/ExChangePaper/ExChangePaper'
import CategoryShopList from '../../components/CategoryShopList/CategoryShopList'
import ActivityShopList from '../../components/ActivityShopList/ActivityShopList'
import GameList from '../../components/GameList/GameList'
import Game from '../../components/Game/Game'
import shop from '../../components/shop/shop'
import ShopDetail from '../../components/Shop_detail/Shop_detail'
import Search from '../../components/Search/Search'
import CitySelect from '../../components/CitySelect/CitySelect'
import SelectCity from '../../components/SelelctCity/SelectCity'
import '../../common/reset.scss'
import store from '../../common/js/store'
Vue.use(Router);

const router=new Router({
    routes: [
      {
        path: '/',
        redirect: '/home'//
      },
        {
            path: "/home",
            name: "home",
            component: Home2,
            children: [
            {
              path: ':id',
              name:'shop',
              component: shop
            }
          ]
        },
        {
            path: "/me",
            name: 'me',
            component: Me,
        },
      {
        path: "/shop/:id",
        name: 'ShopDetail',
        component: ShopDetail,
      },

      {
        path: "/picture",
        name: 'picture',
        component: picture,
      },
      {
        path: "/login",
        name: 'login',
        component: LoaginPage,
      },
      {
        path: "/gitpaper",
        name: 'gitpaper',
        component: GiftPaper,
      },
      {
        path: "/exchangepaper",
        name: 'exchangepaper',
        component: ExChangePaper,
      },
      {
        path: "/search",
        name: 'search',
        component: Search,
      },
      {
        path: "/gamelist",
        name: 'gamelist',
        component: GameList,
      },
      {
        path: "/cityselect",
        name: 'cityselect',
        component: CitySelect,
      },
      {
        path: "/selectcity",
        name: 'selectcity',
        component: SelectCity,
      },
      {
        path: "/categoryshoplist/:id",
        name: 'categoryshoplist',
        component: CategoryShopList,
      },
      {
        path: "/activityshoplist/:id",
        name: 'activityshoplist',
        component: ActivityShopList,
      },
      {
        path: "/game",
        name: 'game',
        component: Game,
        meta:{keepAlive:false}
      }
    ],
});

// npm run dev

router.beforeEach((to, from, next) => {
  const nextRoute = ['home', 'me'];
  // nextRoutelogin
  if (nextRoute.indexOf(to.name) >= 0) {
    if(!store.state.isLogin){
      console.log(store.state.isLogin)
       router.replace({
        path: '/login',query:{}
      });
    }
  }
  next();
});

export default router;



