
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

import axios from 'axios'
import Swiper from 'swiper';
import 'swiper/dist/css/swiper.min.css';
export default {
name: "gift-paper",
data(){
return{
scanImg:false,
qrcodeImgUrl:"",
allPaper:[],
activityId:"",
swiperIndex:1,
unUsePaper:[
"https://s1.ax1x.com/2018/07/28/PUdXy8.png",
],
paperIndex:0
}
},
mounted(){
var that=this;
var mySwiper = new Swiper('.swiper-container', {
effect: 'coverflow',
slidesPerView: 2,
centeredSlides: true,
initialSlide: 1,
coverflowEffect: {
rotate: 0,
stretch: 10,
depth: 160,
modifier: 2,
},// 
navigation: {
nextEl: '.swiper-button-next',
prevEl: '.swiper-button-prev',
},
})

mySwiper.on('slideChangeTransitionEnd', function(){
that.swiperIndex=mySwiper.activeIndex
})
},
created(){

},

methods:{
showCode(item){
this.$router.push({
path: `/game`
})
},









back() {
this.$router.go(-1);
},
}
}



