import cityData from "../../common/js/cityData"
import Scroll from "../../components/scroll/scroll"
import Alphabet from '../../components/Alphabet/Alphabet'
export default {
  data () {
    return {
      currView: '',
      visitCityList: [
        {zip: "010", name: ""},
        {zip: "021", name: ""},
      ],
      hotCityList: [
        {zip: "010", name: ""},
        {zip: "021", name: ""},
        {zip: "020", name: ""},
        {zip: "0755", name: ""},
        {zip: "022", name: ""},
        {zip: "028", name: ""},
        {zip: "0571", name: ""},
        {zip: "023", name: ""},
        {zip: "025", name: ""},
      ],
      cityList: [],
      selectCity: {},
    }
  },
  components:{Scroll,Alphabet},
  created() {
    setTimeout(()=>{
      this.initCityList()
    },1000)
    this.currView = ''
  },

  methods: {
    //
    initCityList() {
      this.cityList = cityData
    },

    //
    clickCity(city) {
      this.$emit('cityService', city.name)
    }
  },
}



