if (this.activityData.length > 15) {
this.$refs.loading.style.display = "flex"
this.loading = true;
setTimeout(() => {
var last = this.activityData[this.activityData.length - 1];
for (var i = 1; i <= 10; i++) {
this.activityData.push(last + i);
}
this.$refs.loading.style.display = "none"
this.loading = false;
}, 2500);
}
else {
this.$refs.loading.style.display = "none"
this.loading = false;
}
},
getDate(time) {
var date = new Date(time);
var Y = date.getFullYear() + '-';
var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
var D = date.getDate() + ' ';
var h = date.getHours() + ':';
var m = date.getMinutes() + ':';
var s = date.getSeconds();
return Y + M + D + h + m + s;
},

selectItem(item) {
