export default {
  props: {
    list: Array
  },
  data () {
    return {
      touchstart:false,
      startY:0,
      timer: null
    }
  },
  computed: {
    letter () {
      const letter =[]
      for (let i in this.list) { //A-Z
        letter.push(i)
      }
      return letter
    }
  },
  methods: {
    handeClick(e) {
      this.$emit('chang',e.target.innerText) //City
    },
    handleTouchStart () {
      // 
      this.touchstart = true
    },
    handleTouchMove (e) {
      // 
      if(this.touchstart) {
        if(this.timer) {
          clearInterval(this.timer)
        }
        this.timer = setTimeout(() => {
          const touchY = e.touches[0].clientY -79   //
          const index = Math.floor((touchY - this.startY ) / 20)
          if(index >=0 && index < this.letter.length) {
            this.$emit('chang',this.letter[index])
          }
        },16)
      }
    },
    handleTouchEnd () {
      // 
      this.touchstart = false
    }
  }
}



