import axios from 'axios'
import Scroll from "../../components/scroll/scroll"
import Loading from '../../components/loading/loading'
export default {
  name: "gift-paper",
  components:{Scroll,Loading},
  data(){
    return{
      id:"",
      pullup: true,
      dataList:[],
      imgUrls1: [
        'http://www.powerdekor.com.cn/indexStyle/images/public/logo.png'
      ],
      imgUrls2: [
        'http://www.marcopolo.com.cn/static/images/static/logo.png'
      ],
      imgUrls3: [
        'https://ss0.bdstatic.com/-0U0bnSm1A5BphGlnYG/tam-ogel/73c19242d8c23028c06b0bea751cddac_121_121.jpg'
      ],
    }
  },
  mounted(){
  },
  created(){
    this.id=this.$route.params.id;
  },

  methods:{
    searchMore() {
      if (this.dataList.length > 15) {
        this.$refs.loading.style.display = "flex"
        this.loading = true;
        setTimeout(() => {
          var last = this.dataList[this.dataList.length - 1];
          for (var i = 1; i <= 10; i++) {
            this.dataList.push(last + i);
          }
          this.$refs.loading.style.display = "none"
          this.loading = false;
        }, 2500);
      }
      else {
        this.$refs.loading.style.display = "none"
        this.loading = false;
      }
    },
    selectItem(item){
      this.$router.push({
        path: `/shop/${item}`
      })
    },
    //
    back() {
      this.$router.go(-1);
      this.$destroy();
    },
  }
}



