import city from "../../components/City/city"
import Scroll from "../../components/scroll/scroll"
import cityData from "../../common/js/cityData"
import axios from 'axios'
export default {
  name: "city-select",
  components: {city,Scroll},
  data(){
    return{
      selectCity: {},
      cityList: [],
    }
  },
  mounted(){
    this.$nextTick(()=>{
      this.$refs.scroll.refresh();
    })
  },
  created(){
    setTimeout(()=>{
      this.initCityList()
    },1000)
  },
  methods: {
    //
    back() {
      this.$router.go(-1);
      this.$destroy()
    },
    //
    initCityList() {
      this.cityList = cityData
    },

    //  citycity
    clickCity(city) {
      // Vuex statecurCity
      this.$store.commit('updateCity', city.name);
      this.$store.commit('updateCode', city.zip);
      this.$router.go(-1);
    }
  },
}



